___CSS_LOADER_EXPORT___.push([module.id, `body {
    font-family: 'Segoe UI', sans-serif;
  
    font-weight: bold;
    /* background-color: #313131; */
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  
    margin: 0;
  }
  
  .conteudo {
    display: flex;
    flex-direction: column;
    padding: 0 5%;
  }
  
  h1 {
    font-size: 3rem;
    font-weight: 700;
    color: #0a0808;
  }
  
  .descricao {
    font-size: 22px;
    font-weight: 600;
    color: #0a0808;
  }
  
  .container-formulario {
    display: flex;
    /* justify-content: center; */
    gap: 5px;
  
    padding: 1%;
  }
  
  .pesquisa {
    outline: 1px solid #f9f9f9;
  
    border:none;
    border-radius: 10px;
  
    font-size: 18px;
  
    padding: 5px;
  }
  
  .pesquisa-erro {
    outline: 2px solid #DA471B !important;
  }
  
  .btn {
    display: grid;
    padding: 15px;
  
    border: none;
    border-radius: 10px;
  }
  
  .btn:hover {
    opacity: 0.9;
    cursor: pointer;
    outline: 1px solid #ffffff;
  }
  
  .btn-pesquisar {
    background-color: #4DAD5B;
    color: white;
  }
  
  .btn-limpar {
    background-color: #DA471B;
    color: white;
  }
  
  .card-pokemon {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  
    height: 450px;
  
    background-color: #e1e1e1e7;
    color: #313131;
    border-radius: 1rem;
    border-width: 6px;
    border-style: solid;
    border-color:#000000;
  
    border-radius: 1rem;
  
    margin-top: 5%;
  }
  .card-pokemon > p{
    background-color: #e1e1e1c2;
    color: #313131;
    border-width: 1px;
    border-style:solid;
    border-color:#4d4b4d;
    
  }
  .card-pokemon > img{
    background-color: #e1e1e1c2;
    color: #313131;
    border-width: 1px;
    border-color:#f8f8f8;
    border-style:solid;
  }
  .notificacao {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
  
    margin-left: auto;
    margin-right: auto;
  
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    
    background-color: #DA471B;
    border-radius: 10px;
  
    width: 300px;
  
    padding: 10px 20px;
  }
  
  .notificacao:hover {
    opacity: 0.9;
    cursor: pointer;
  }
